@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@200&display=swap');
*{
    padding: 0;
    margin: 0;
    font-family: 'Poppins', sans-serif;
}
:root{
    --primary-color: rgb(88, 38, 4);
    --secondary-color: rgb(240, 197, 142);
}
body{
    background-color: var(--primary-color);

}
html{
    font-size: 65.5%;
}
.header{
    color: var(--secondary-color);
    box-shadow: 0 5px 10px var(--secondary-color);
    display: grid;
    grid-template-columns: auto auto;
    align-items: center;
      
}
.row1{
    justify-content:start;
}
.row2{
   display: flex;
   flex-direction: column;
   align-items: center;
   justify-content: center;
}
.row2 h2{
    font-size: 3rem;
    margin-top: 20px;
    margin-bottom: 15px;
}
.row2 img{
    margin-top: 30px;
    width: 250px;
}
.row1 h1{
    font-size: 3rem;
    text-align: center;
    margin-left: 35px;
    font-family: 'Poppins', sans-serif;
}
.search{
    display: flex;
    align-items: center;
    justify-content: center;
}
.search input{
    outline: 0;
    border: 3px;
    width: 30rem;
    height: 35px;
    padding: 0 0.5rem;
    font-size: 15px;
    font-weight: bold;
}
.search button{
    outline: none;
    border: none;
    height: 35px;
    padding: 0 0.5rem;
}
.container{
    width: 65%;
    margin: 0 auto;
    margin-top: 25px;
    display: grid;
    grid-template-columns: repeat(auto-fit,minmax(200px,0.5fr));
    grid-gap: 20px;
    box-sizing: border-box;
}
.card{
    background-color: var(--secondary-color);
    border-radius: 3rem;
    padding: 0.5rem;
    margin-bottom: 10px;
    box-shadow: 0 5px 5px rgba(212,185,150,0.5);
    transition: 0.5s;
    text-align: center;
    position: relative;
}
.card:hover{
    transform: scale(0.9);
    background-color: rgba(255,255,255,0.8);
}
.card img{
    width: 100%;
    height: 200px;
    border-radius: 1rem 1rem 0 0;
}
.card .bottom{
    display: flex;
    flex-direction: column;
}
.card .bottom .title{
    font-size: 14px;
    margin-bottom: 12px;
}
.card .bottom .amount{
    position: absolute;
    bottom: 10px;
    left: 10px;
    right: 10px;
    background-color: var(--primary-color);
    color: var(--secondary-color);
    font-size: 13px;
    font-weight: bold;
    padding: 0.2rem;
}
.overlay{
    min-height: 100vh;
    width: 100%;
    position: fixed;
    left: 0;
    top: 0;
    bottom: 0;
    right: 0;
    background: linear-gradient(rgba(0,0,0,0.2),rgba(0,0,0,0.1));
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 1000;
}
.overlay-inner{
    background: white;
    height: 100%;
    width: 100%;
    padding: 1.5rem;
    position: relative;
    box-sizing: border-box;
    overflow: hidden;
    font-size: 1.3rem;
}
.overlay-inner .close{
    position: absolute;
    top: 1rem;
    right: 1rem;
    outline: none;
    border: none;
    font-size: 17px;
    background-color: transparent;
}
.overlay-inner .inner-box{
    margin-top: 30px;
    display: flex;
    justify-content: center;
}
.overlay-inner .inner-box img{
    
    margin-left: 20px;
    width: 150px;
    height: 200px;
}
.overlay-inner .inner-box h1{
    margin-left:15px;
}
.overlay-inner .inner-box h3{
    margin-left: 15px;
    margin-top: 10px;
    color: green;
}
.overlay-inner .inner-box h4{
    margin-left: 15px;
    color:blue;
}
.overlay-inner .inner-box button{
    margin-left: 15px;
    outline: none;
    border: none;
    width: 100px;
    border-radius: 5px;
    background-color: aqua;
    color:white;
    font-weight: bold;
    padding: 0.3rem 0.5rem;
}
.overlay-inner .description{
    margin-top: 2rem;
    text-align: justify;
    font-size: 13px;
}
.loading {
    width: 100vw;
    height: 100vh;
    background-color: var(--primary-color);
    display: grid;
    place-items: center;
    max-width: 100%;
  }
  
  .loading > div {
    width: 10vmax;
    height: 10vmax;
    border-bottom: 5px solid rgba(0, 0, 0, 0.719);
  
    border-radius: 50%;
  
    animation: loadingRotate 800ms linear infinite;
  }
  
  @keyframes loadingRotate {
    to {
      transform: rotateZ(-360deg);
    }
  }  

  @media only screen and (max-width: 550px) {
    .header{
        color: var(--secondary-color);
        box-shadow: 0 5px 10px var(--secondary-color);
        display: grid;
        grid-template-columns: auto;
        align-items: center;
          
    }
    .row1{
        display: none;
    }
    .container{
        width: 85%;
        margin: 0 auto;
        margin-top: 25px;
        display: grid;
        grid-template-columns: repeat(auto-fit,minmax(200px,1fr));
        grid-gap: 20px;
        box-sizing: border-box;
    }
    .overlay-inner{
        margin-top: 5rem;
    }
  }